<template>
    <div class="card-container">
        <a-tabs :activeKey="category.id.toString()" @change="handleClick">
            <a-tab-pane :tab="row.name"
                        :key="row.id.toString()"
                        v-for="(row,index) in siblings"
                        @click="handleClick(index)">
                <template v-if="row.id===category.id">
                    <table-component
                            ref="table"
                            @handleOperate="handleOperate"
                            :columns="columns"
                            :request="request"
                            :requestInitFilter="requestInitFilter">
                    </table-component>
                </template>
            </a-tab-pane>
        </a-tabs>
    </div>
</template>

<script>
    import tableComponent from '@/components/table'

    export default {
        components: {tableComponent},
        props: {
            category: {required: true, type: Object},
            request: {
                required: true,
            },
            columns:{
                required:false,
                type: Array,
                default(){
                    return [];
                }
            }
        },
        computed: {
            categoryId() {
                if (this.category.hasOwnProperty('id')) {
                    return this.category.id
                } else {
                    return -1
                }
            },
            siblings() {
                if (this.category.hasOwnProperty('siblings') && this.category.siblings) {
                    return this.category.siblings
                } else {
                    return []
                }
            },
            requestInitFilter() {
                return {category_id: this.categoryId}
            }
        },
        data() {
            return {
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '标题',
                        dataIndex: 'post_title',
                        key: 'post_title',
                    },
                    {
                        title: '审核状态',
                        dataIndex: 'audit_status_format',
                        key: 'audit_status_format',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            check: {visible: true, disable: false,},
                            edit: {visible: true, disable: false,},
                            audit: {visible: true, disable: false,},
                            delete: {visible: true, disable: false,},
                        }
                    }
                ],
            }
        },
        methods: {
            handleClick(id) {
                this.$emit('setId', id);
            },
            handleOperate(type, row) {
                this.$emit('handleOperate', type, row);
            },
            reFetch() {
                this.$refs.table.reFetch();
            },
        },
    }
</script>
<style>
    .card-container {
        background: white;
        overflow: hidden;
        padding: 10px;
    }

    .card-container > .ant-tabs-card > .ant-tabs-content {
        margin-top: -16px;
    }

    .card-container > .ant-tabs-card > .ant-tabs-content > .ant-tabs-tabpane {
        background: #fff;
        padding: 16px;
    }

    .card-container > .ant-tabs-card > .ant-tabs-bar {
        border-color: #fff;
    }

    .card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab {
        border-color: transparent;
        background: transparent;
    }

    .card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab-active {
        border-color: #fff;
        background: #fff;
    }
</style>
