<template>
    <components
            ref="component"
            :category="category"
            :request="request"
            :columns="columns"
            :is="listTemplate"
            :template="listTemplate"
            @setId="setId"
            @handleOperate="handleOperate"/>
</template>

<script>
    import list from './components/list/Index'
    import list_online_learning from './components/list_online_learning/Index'
    import list_news from './components/list_news/Index'
    import {getCategory} from '@/api/category'

    export default {
        name: "Index",
        components: {list, list_online_learning, list_news},
        props: {
            id: {
                required: true,
                type: [Number, String]
            },
            request: {
                require: true,
            },
            columns: {
                required: false,
                type: Array,
                default() {
                    return [];
                }
            }
        },
        data() {
            return {
                listTemplate: '',
                category: {},
                thisId: null,
            }
        },
        methods: {
            setId(id = null) {
                //初始化调用prop传入的id
                //子组件中冒泡出id重新获取数据
                if (id) {
                    this.thisId = id;
                } else {
                    this.thisId = parseInt(this.id);
                }
                this.fetch();
            },
            fetch() {
                if (this.thisId === null) {
                    return false;
                }
                getCategory(this.thisId)
                    .then(res => {
                        this.listTemplate = res.data.list_template;
                        this.category = res.data;
                    })
            },
            reFetch() {
                this.listTemplate = null;
                this.category = null;
                this.fetch();
            },
            handleOperate(type, row) {
                //给上层组件调用的方法
                this.$emit('handleOperate', type, row);
            },
        },
        watch: {
            $route: {
                handler() {
                    this.setId();
                },
                immediate: true
            }
        }
    }
</script>

